.Request{
    margin-top: 50px;
}

.Request__form{
    margin-top: 20px;
}

.Request label{
    font-weight: 600;
    font-size: 20px;
}

.Request label:not(:first-child){
    margin-top: 10px;
}

.Request input:not(:last-child){
    outline: none;
    width: 100%;
    color: black;
    font-size: 18px;
    padding-left: 10px;
    padding-right: 5px;
    border: 2px solid #0a0e11;
    border-radius: 10px;
}

.Request textarea{
    outline: none;
    height: 200px;
    font-size: 18px;
    padding-left: 10px;
    padding-right: 5px;
    width: 100%;
    border: 2px solid #0a0e11;
    border-radius: 10px;
}

.clip{
    width: 16px;
    height: 16px;
}

.Request input[type=file]{
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.label--last{
    cursor: pointer;
    position: relative;
}

.label--last:hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 80%;
    height: 13px;
    z-index: -1;
    display: block;
    background-color: rgb(66, 142, 255);
    border-radius: 2px;
}

.Request__button{
    outline: none;
    border: 2px solid #0a0e11;
    border-radius: 3px;
    padding: 5px 20px;
    background-color: white;
}

/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .label--last:hover:after{
        content: "";
        position: absolute;
        left: -10px;
        bottom: 0;
        width: 120%;
        height: 13px;
        z-index: -1;
        display: block;
        background-color: rgb(66, 142, 255);
        border-radius: 2px;
    }
}

