.Partners{
    margin-top: 50px;
}

.Partners__subtitle{
    margin-top: 25px;
    font-size: 20px;
    color: #000000;
    font-weight: 500;
}

.Partners__description{
    margin-top: 75px;
    font-size: 18px;
    color: #000000;
}

.Partners__list{
    margin-top: 65px;
    margin-left: -40px;
    font-size: 16px;
    font-weight: 500;
    color: #000000;
}

.Partners__advantages{
    margin-top: 35px;
}

.Partners__advantages--icon{
    margin-top: 40px;
    width: 60%;
    height: 100px;
}

.Partners__advantages--title{
    font-weight: 500;
}


.Partners__scramble a{
    margin-top: -80px;
}



/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Partners{
        margin-top: 50px;
    }

    .Partners__subtitle{
        margin-left: 15px;
        font-size: 18px;
        font-weight: 600;
    }

    .Partners__description{
        margin-top: 25px;
        font-size: 16px;
        color: #000000;
        line-height: 1.3;
    }

    .Partners__list{
        list-style: none;
        margin-top: 25px;
        margin-left: -40px;
        font-size: 16px;
        font-weight: 400;
        color: #000000;
        line-height: 1.2;
    }

    .Partners__list li{
        margin-top: 10px;
    }

    .Partners__advantages{
        margin-top: 5px;
    }

    .Partners__advantages--icon{
        margin-top: 20px;
        width: 60%;
        height: 100px;
    }

    .Partners__advantages--title{
        font-weight: 600;
    }


    .Partners__scramble a{
        margin-top: 0;
    }

}

