.Sentence{
    margin-top: 50px;
}

.Sentence__number{
    margin-top: 40px;
    font-weight: bold;
    font-size: 14px;
}

.Sentence__name{
    font-weight: bold;
    font-size: 28px;
}

/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Sentence{
        margin-top: 50px;
    }

    .Sentence__number{
        margin-top: 15px;
        font-weight: bold;
        font-size: 14px;
    }

    .Sentence__name{
        font-weight: bold;
        font-size: 18px;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Sentence{
        margin-top: 50px;
    }

    .Sentence__number{
        margin-top: 15px;
        font-weight: bold;
        font-size: 14px;
    }

    .Sentence__name{
        font-weight: bold;
        font-size: 18px;
    }
}

