.Projects{
    margin-top: 50px;
}

.Projects__title{
    font-weight: 700;
    font-size: 50px;
    margin-top: -50px;
}

.Projects__item{
    margin-top: 120px;
}

.Projects__item a{
    color: black;
}

.Projects__item a:hover{
    color: black;
    text-decoration: none;
}

.Projects__img{
    width: 100%;
}

.Projects__name{
    font-size: 50px;
    font-weight: 700;
}

.Projects__work{
    color: #727272;
    font-size: 16px;
}

.Projects__description{
    font-size: 20px;
    font-weight: 600;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Projects{
        margin-top: 50px;
    }

    .Projects__title{
        font-weight: 700;
        font-size: 38px;
        margin-top: 0px;
        line-height: 1.1;
    }

    .Projects__item{
        margin-top: 20px;
    }

    .Projects__item a{
        color: black;
    }


    .Projects__img{
        margin-top: 15px;
        width: 100%;
    }

    .Projects__name{
        font-size: 30px;
        line-height: 1;
        font-weight: 600;
    }

    .Projects__work{
        color: #727272;
        font-size: 14px;
    }

    .Projects__description{
        font-size: 16px;
        font-weight: 600;
    }
}

