.Vacancies{
    margin-top: 50px;
}

.Vacancies__subtitle{
    margin-top: 40px;
    font-size: 28px;
    font-weight: bold;
}

.Vacancies__name{
    font-size: 26px;
    font-weight: bold;
    cursor: pointer;
    -webkit-touch-callout: none; /* Выделение */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Vacancies__item{
    margin-top: 25px;
}

.Vacancies__description{
    margin-top: 25px;
}

/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Vacancies{
        margin-top: 50px;
    }

    .Vacancies__subtitle{
        line-height: 1.3;
        margin-top: 20px;
        font-size: 22px;
        font-weight: bold;
    }

    .Vacancies__name{
        font-size: 18px;
        cursor: pointer;
        -webkit-touch-callout: none; /* Выделение */
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    .Vacancies__item{
        margin-top: 15px;
    }

    .Vacancies__description{
        margin-top: 15px;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Vacancies{
        margin-top: 50px;
    }

    .Vacancies__subtitle{
        line-height: 1.3;
        margin-top: 20px;
        font-size: 22px;
        font-weight: bold;
    }

    .Vacancies__name{
        font-size: 18px;
        cursor: pointer;
        -webkit-touch-callout: none; /* Выделение */
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    .Vacancies__item{
        margin-top: 15px;
    }

    .Vacancies__description{
        margin-top: 15px;
    }
}

