.Home{
    margin-top: 150px;
}

h1{
    margin-left: auto;
    margin-right: auto;
    font-size: 76px !important;
}

.Home__banner{
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
}

.Home__banner img{
    width: 100%;
    height: 400px;
    border-radius: 10px;
}

.Home__owl{
    width: 100%;
    cursor: pointer;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Home{
        margin-top: 50px;
    }

    h1{
        margin-left: auto;
        margin-right: auto;
        font-size: 36px !important;
    }

    .Home__banner{
        margin-top: 40px;
    }

    .Home__banner img{
        width: 100%;
        height: 300px;
        border-radius: 10px;
    }

    .Home__owl{
        width: 100%;
        cursor: pointer;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Home{
        margin-top: 70px;
    }

    h1{
        margin-left: auto;
        margin-right: auto;
        font-size: 40px !important;
    }

    .Home__banner{
        margin-top: 40px;
    }

    .Home__banner img{
        width: 100%;
        height: 400px;
        border-radius: 10px;
    }

    .Home__owl{
        width: 100%;
        cursor: pointer;
    }
}

/*/Средние девайсы («таблетки», >= 768px)*/
@media (min-width: 768px) and (max-width: 991.999px) {
    .Home{
        margin-top: 100px;
    }

    h1{
        margin-left: auto;
        margin-right: auto;
        font-size: 50px !important;
    }

    .Home__banner{
        margin-top: 60px;
    }

    .Home__banner img{
        width: 100%;
        height: 450px;
        border-radius: 10px;
    }

    .Home__owl{
        width: 100%;
        cursor: pointer;
    }
}

