.ProjectItem__img{
    width: 100%;
}

.ProjectItem__title{
    font-size: 50px;
    font-weight: 700;
}

.ProjectItem__work{
    color: #727272;
    font-size: 16px;
}

.ProjectItem__description{
    font-size: 20px;
    font-weight: 600;
}

.ProjectItem__link{
    margin-top: 30px;
}

.ProjectItem__link a{
    font-size: 20px;
    font-weight: 600;
    color: black;
}

.ProjectItem__link a:hover{
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
    color: #727272;
}

/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .ProjectItem__title{
        font-size: 32px;
        font-weight: 700;
    }

    .ProjectItem__work{
        color: #727272;
        font-size: 16px;
    }

    .ProjectItem__description{
        font-size: 18px;
        font-weight: 600;
    }

    .ProjectItem__link{
        margin-top: 15px;
    }

    .ProjectItem__link a{
        font-size: 20px;
        font-weight: 600;
        color: black;
    }

    .ProjectItem__link a:hover{
        font-size: 20px;
        font-weight: 600;
        text-decoration: none;
        color: #727272;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .ProjectItem__title{
        font-size: 36px;
        font-weight: 700;
    }

    .ProjectItem__work{
        color: #727272;
        font-size: 16px;
    }

    .ProjectItem__description{
        font-size: 18px;
        font-weight: 600;
    }

    .ProjectItem__link{
        margin-top: 15px;
    }

    .ProjectItem__link a{
        font-size: 20px;
        font-weight: 600;
        color: black;
    }

    .ProjectItem__link a:hover{
        font-size: 20px;
        font-weight: 600;
        text-decoration: none;
        color: #727272;
    }
}

