.Footer{
    margin-top: 100px;
}

.Footer__left{
    margin-left: 50px;
}

.Footer__left a{
    color: black;
    font-size: 18px;
}

.Footer__left a:hover{
    color: #727272;
    text-decoration: none;
}

.Footer a{
    color: black;
    font-size: 18px;
}

.Footer a:hover{
    color: #727272;
    text-decoration: none;
}

.Footer__right{
    margin-right: 40px;
}
.Footer__social{
    width: 45px;
    margin-right: 20px;
}

.Footer__center a{
    margin-left: 50px;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Footer{
        margin-top: 50px;
    }

    .Footer__left{
        margin-left: 25px;
        margin-top: 35px;
    }

    .Footer__right{
        margin-left: 25px;
    }
    .Footer__social{
        width: 40px;
        margin-right: 30px;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Footer{
        margin-top: 50px;
    }

    .Footer__left{
        margin-left: 35px;
    }

    .Footer__right{
        margin-top: 5px;
    }
    .Footer__social{
        width: 35px;
        margin-right: 10px;
    }
}

