.Services{
    margin-top: 150px;
}

.Services__title{
    color: black;
    font-weight: 700;
    font-size: 18px;
    margin-top: -38px;
}

.Services__left{
    margin-left: 40px;
}

.Services__item{
    font-size: 75px;
    font-weight: 700;
}

.Services__number{
    font-size: 20px;
    margin-top: 25px;
}

.Services__slogan{
    font-size: 46px;
}

/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Services{
        margin-top: 80px;
        margin-left: -25px;
    }

    .Services__title{
        color: black;
        font-weight: 700;
        font-size: 22px;
    }

    .Services__left{
        margin-left: 30px;
    }

    .Services__item{
        margin-top: 15px;
        font-size: 28px;
        font-weight: 700;
    }

    .Services__number{
        font-size: 16px;
        margin-top: 0;
    }

    .Services__slogan{
        font-size: 18px;
        margin-left: 30px;
        margin-top: 20px;
        line-height: 1.5;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Services{
        margin-top: 80px;
        margin-left: -25px;
    }

    .Services__title{
        color: black;
        font-weight: 700;
        font-size: 22px;
    }

    .Services__left{
        margin-left: 40px;
    }

    .Services__item{
        margin-top: 15px;
        font-size: 36px;
        font-weight: 700;
    }

    .Services__number{
        font-size: 16px;
        margin-top: 0;
    }

    .Services__slogan{
        font-size: 22px;
        margin-left: 25px;
        margin-top: 20px;
    }
}

/*/Средние девайсы («таблетки», >= 768px)*/
@media (min-width: 768px) and (max-width: 991.999px) {
    .Services{
        margin-top: 80px;
        margin-left: -25px;
    }

    .Services__title{
        color: black;
        font-weight: 700;
        font-size: 22px;
    }

    .Services__left{
        margin-left: 40px;
    }

    .Services__item{
        margin-top: 15px;
        font-size: 36px;
        font-weight: 700;
    }

    .Services__number{
        font-size: 16px;
        margin-top: 0;
    }

    .Services__slogan{
        font-size: 22px;
        margin-left: 25px;
        margin-top: 20px;
    }
}

