.Project{
    margin-top: 280px;
}

.Project__slogan{
    color: black;
    font-weight: 700;
    font-size: 18px;
    margin-top: -100px;
}

.Project__image{
    margin-top: 100px;
    width: 100%;
    display: none;
}

.img-active{
    width: 100%;
    display: block;
}

.Project__link ul{
    margin-top: -100px;
    list-style: none;
}

.Project__link li{
    margin-left: 10px;
    transition: all 1s;
}

.Project__link li:not(:first-child){
    margin-top: 50px;
}

.Project__link li:hover{
    margin-left: 80px;
}

.Project__link li a{
    font-size: 46px;
    font-weight: 600;
    color: #727272;
}

.Project__link li a:hover{
    transition: 500ms;
    font-size: 46px;
    font-weight: 600;
    color: black;
    text-decoration: none;
}

.Project__description{
    font-size: 20px;
}

.Project__all{
     margin-top: 25px;
     color: black;
 }

.Project__all a{
    color: black;
    font-size: 22px;
    position: relative;
}

.Project__all a:hover{
    color: black;
    font-size: 22px;
    text-decoration: none;
}

.Project__all1 :hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 115%;
    height: 15px;
    z-index: -1;
    display: block;
    background-color: rgb(66, 142, 255);
    border-radius: 2px;
}

.Project__all2 :hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 115%;
    height: 15px;
    z-index: -1;
    display: block;
    background-color: rgb(255, 148, 54);
    border-radius: 2px;
}

.Project__all3 :hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 115%;
    height: 15px;
    z-index: -1;
    display: block;
    background-color: rgb(0, 255, 92);
    border-radius: 2px;
}

.Project__all4 :hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 115%;
    height: 15px;
    z-index: -1;
    display: block;
    background-color: rgb(255, 3, 12);
    border-radius: 2px;
}


