.ScrambleText{
    margin-top: 150px;
    position: relative;
}

.ScrambleText a{
    color: black;
}

.ScrambleText a:hover{
    color: black;
    text-decoration: none;
}

.ScrambleText span{
    font-size: 70px;
    font-weight: 600;
}

.ScrambleText:hover:after{
    content: "";
    position: absolute;
    bottom: 0;
    height: 45px;
    width: 700px;
    background-color: #ff9436;
    z-index: -1;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {

    .ScrambleText{
        margin-top: 30px;
        margin-left: 10px;
        text-align: center;
    }

    .ScrambleText span{
        font-size: 36px;
        font-weight: 600;
        line-height: 1;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .ScrambleText{
        margin-top: 50px;
    }

    .ScrambleText span{
        font-size: 42px;
        font-weight: 600;
    }
}

/*/Средние девайсы («таблетки», >= 768px)*/
@media (min-width: 768px) and (max-width: 991.999px) {
    .ScrambleText{
        margin-top: 70px;
        position: relative;
    }

    .ScrambleText span{
        font-size: 50px;
        font-weight: 600;
    }
}

