.Contacts{
    margin-top: 50px;
}

.Contacts__subtitle{
    margin-top: 40px;
    font-size: 28px;
    font-weight: bold;
}

.Contacts__items{
    margin-top: 20px;
    font-size: 20px;
}

.Contacts__item:not(:first-child){
    margin-top: 20px;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Contacts{
        margin-top: 50px;
    }

    .Contacts__subtitle{
        margin-top: 15px;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.2;
    }

    .Contacts__items{
        margin-top: 15px;
        font-size: 16px;
    }

    .Contacts__item:not(:first-child){
        margin-top: 10px;
    }
}

/*Малые девайсы («ландшафтные телефоны», >= 576px)*/
@media (min-width: 576px) and (max-width: 767.999px) {
    .Contacts{
        margin-top: 50px;
    }

    .Contacts__subtitle{
        margin-top: 15px;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.2;
    }

    .Contacts__items{
        margin-top: 15px;
        font-size: 16px;
    }

    .Contacts__item:not(:first-child){
        margin-top: 10px;
    }
}

/*/Средние девайсы («таблетки», >= 768px)*/
@media (min-width: 768px) and (max-width: 991.999px) {
    .Contacts{
        margin-top: 50px;
    }

    .Contacts__subtitle{
        margin-top: 15px;
        font-size: 18px;
        font-weight: bold;
        line-height: 1.2;
    }

    .Contacts__items{
        margin-top: 15px;
        font-size: 16px;
    }

    .Contacts__item:not(:first-child){
        margin-top: 10px;
    }
}

