    color: black;
}


/* Меньше (<576px)*/
@media (max-width: 575.999px) {
    .Header{
        position: relative;
    }

    .Header__logo{
        z-index: 11;
        margin-top: 15px;
        margin-left: auto;
        margin-right: auto;
    }
    .Header__logo a img{

        width: 75px;
    }
    .Header__toggle{
        position: absolute;
        top: 0;
        right: 0;
        margin-top: 15px;
        margin-right: 20px;
        font-size: 35px;
        z-index: 11;
    }

    .Header__menu{
        position: absolute;
        z-index: 10;
        background-color: white;
        font-weight: 600;
        font-size: 18px;
        width: 100%;
        top: -40px;
        left: 0;
    }

    .Header__menu:after{
        position: absolute;
        content: "";
        background-color: white;
        height: 2100px;
        width: 100%;
    }


    .Header__menu ul{
        margin-top: 120px;
    }


}

