    width: 100%;
    display: block;
}

.Project__link ul{
    margin-top: -100px;
    list-style: none;
}

.Project__link li{
    margin-left: 10px;
    transition: all 1s;
}

.Project__link li:not(:first-child){
    margin-top: 50px;
}

.Project__link li:hover{
    margin-left: 80px;
}

.Project__link li a{
    font-size: 46px;
    font-weight: 600;
    color: #727272;
}

.Project__link li a:hover{
    transition: 500ms;
    font-size: 46px;
    font-weight: 600;
    color: black;
    text-decoration: none;
}

.Project__description{
    font-size: 20px;
}

.Project__all{
     margin-top: 25px;
     color: black;
 }

.Project__all a{
    color: black;
