    position: relative;
}

.Project__all a:hover{
    color: black;
    font-size: 22px;
    text-decoration: none;
}

.Project__all1 :hover:after{
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    width: 115%;
    height: 15px;
